
"use client";

import { useEffect, useState, useRef } from "react";
import { useRouter } from "next/navigation";
import type { WorkoutRoutine } from "@/lib/workouts";
import { Card, CardHeader, CardTitle, CardDescription, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { ArrowRight, PlusCircle, Trash2 } from "lucide-react";
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle } from "@/components/ui/alert-dialog";


export function WorkoutSelector({ routines }: { routines: WorkoutRoutine[] }) {
  const [lastWorkout, setLastWorkout] = useState<string | null>(null);
  const [allRoutines, setAllRoutines] = useState<WorkoutRoutine[]>(routines);
  const [routineToDelete, setRoutineToDelete] = useState<WorkoutRoutine | null>(null);
  const router = useRouter();

  const dragItem = useRef<string | null>(null);
  const dragOverItem = useRef<string | null>(null);

  useEffect(() => {
    const savedWorkout = localStorage.getItem("lastWorkout");
    if (savedWorkout) {
      setLastWorkout(savedWorkout);
    }

    const deletedPresetsStr = localStorage.getItem("deletedPresetWorkouts");
    const deletedPresetIds: string[] = deletedPresetsStr ? JSON.parse(deletedPresetsStr) : [];
    const visiblePresetRoutines = routines.filter(r => !deletedPresetIds.includes(r.id));

    const customWorkoutsStr = localStorage.getItem("customWorkouts");
    const customWorkouts: WorkoutRoutine[] = customWorkoutsStr ? JSON.parse(customWorkoutsStr) : [];
    
    const combinedRoutines = [...visiblePresetRoutines, ...customWorkouts];

    const workoutOrderStr = localStorage.getItem("workoutOrder");
    if (workoutOrderStr) {
      try {
        const workoutOrder: string[] = JSON.parse(workoutOrderStr);
        const routineMap = new Map(combinedRoutines.map(r => [r.id, r]));

        const sortedRoutines = workoutOrder
          .map(id => routineMap.get(id))
          .filter(Boolean) as WorkoutRoutine[];
        
        const unsortedRoutines = combinedRoutines.filter(r => !workoutOrder.includes(r.id));
        setAllRoutines([...sortedRoutines, ...unsortedRoutines]);
      } catch (e) {
        console.error("Failed to parse workout order from localStorage", e);
        setAllRoutines(combinedRoutines);
      }
    } else {
      setAllRoutines(combinedRoutines);
    }
  }, [routines]);

  const handleSelectWorkout = (id: string) => {
    localStorage.setItem("lastWorkout", id);
    router.push(`/workout/${id}`);
  };

  const handleCreateWorkout = () => {
    router.push('/workout/new');
  };
  
  const saveOrder = (routinesToSave: WorkoutRoutine[]) => {
    const order = routinesToSave.map(r => r.id);
    localStorage.setItem("workoutOrder", JSON.stringify(order));
  }

  const handleDeleteWorkout = () => {
    if (!routineToDelete) return;

    if (routineToDelete.id.startsWith('custom-')) {
        const customWorkoutsStr = localStorage.getItem("customWorkouts");
        if (customWorkoutsStr) {
            const customWorkouts: WorkoutRoutine[] = JSON.parse(customWorkoutsStr);
            const updatedWorkouts = customWorkouts.filter(w => w.id !== routineToDelete.id);
            localStorage.setItem("customWorkouts", JSON.stringify(updatedWorkouts));
        }
    } else {
        const deletedPresetsStr = localStorage.getItem("deletedPresetWorkouts");
        const deletedPresetIds: string[] = deletedPresetsStr ? JSON.parse(deletedPresetsStr) : [];
        if (!deletedPresetIds.includes(routineToDelete.id)) {
            deletedPresetIds.push(routineToDelete.id);
            localStorage.setItem("deletedPresetWorkouts", JSON.stringify(deletedPresetIds));
        }
    }
    
    const updatedAllRoutines = allRoutines.filter(r => r.id !== routineToDelete.id);
    setAllRoutines(updatedAllRoutines);
    saveOrder(updatedAllRoutines);
    setRoutineToDelete(null);
  };

  const handleDragStart = (e: React.DragEvent<HTMLDivElement>, id: string) => {
    dragItem.current = id;
    e.dataTransfer.effectAllowed = 'move';
  };

  const handleDragEnter = (e: React.DragEvent<HTMLDivElement>, id: string) => {
    e.preventDefault();
    dragOverItem.current = id;
  };

  const handleDragEnd = () => {
    if (!dragItem.current || !dragOverItem.current || dragItem.current === dragOverItem.current) {
        dragItem.current = null;
        dragOverItem.current = null;
        return;
    }

    const dragItemIndex = allRoutines.findIndex(r => r.id === dragItem.current);
    const dragOverItemIndex = allRoutines.findIndex(r => r.id === dragOverItem.current);

    if (dragItemIndex === -1 || dragOverItemIndex === -1) return;

    const newRoutines = [...allRoutines];
    const [draggedItem] = newRoutines.splice(dragItemIndex, 1);
    newRoutines.splice(dragOverItemIndex, 0, draggedItem);

    setAllRoutines(newRoutines);
    saveOrder(newRoutines);

    dragItem.current = null;
    dragOverItem.current = null;
  };

  return (
    <>
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 w-full max-w-5xl">
        {allRoutines.map((routine) => (
          <Card
            key={routine.id}
            draggable
            onDragStart={(e) => handleDragStart(e, routine.id)}
            onDragEnter={(e) => handleDragEnter(e, routine.id)}
            onDragEnd={handleDragEnd}
            onDragOver={(e) => e.preventDefault()}
            className="flex flex-col justify-between hover:border-primary transition-all duration-300 cursor-move"
          >
            <CardHeader>
              <div className="flex justify-between items-start">
                <CardTitle className="font-headline">{routine.name}</CardTitle>
                 <div className="flex items-center gap-2">
                    {lastWorkout === routine.id && (
                      <Badge variant="outline" className="border-accent text-accent whitespace-nowrap">Last Workout</Badge>
                    )}
                    <Button variant="ghost" size="icon" className="h-6 w-6 text-destructive hover:bg-destructive/10" onClick={(e) => { e.stopPropagation(); setRoutineToDelete(routine); }}>
                        <Trash2 className="h-4 w-4" />
                        <span className="sr-only">Delete</span>
                    </Button>
                 </div>
              </div>
              <CardDescription>{routine.description}</CardDescription>
            </CardHeader>
            <CardContent>
              <Button
                onClick={() => handleSelectWorkout(routine.id)}
                className="w-full bg-accent hover:bg-accent/90 text-accent-foreground"
              >
                Start Workout <ArrowRight className="ml-2 h-4 w-4" />
              </Button>
            </CardContent>
          </Card>
        ))}
        <Card
            className="flex flex-col justify-between hover:border-primary transition-all duration-300"
          >
            <CardHeader>
              <CardTitle className="font-headline">Create New Workout</CardTitle>
              <CardDescription>Build your own custom routine.</CardDescription>
            </CardHeader>
            <CardContent>
              <Button
                onClick={handleCreateWorkout}
                className="w-full"
                variant="outline"
              >
                <PlusCircle className="mr-2 h-4 w-4" />
                Create Routine
              </Button>
            </CardContent>
          </Card>
      </div>
      <AlertDialog open={!!routineToDelete} onOpenChange={(open) => !open && setRoutineToDelete(null)}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>Are you sure?</AlertDialogTitle>
            <AlertDialogDescription>
              This action cannot be undone. This will permanently delete the workout routine "{routineToDelete?.name}".
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel onClick={() => setRoutineToDelete(null)}>Cancel</AlertDialogCancel>
            <AlertDialogAction onClick={handleDeleteWorkout} className="bg-destructive text-destructive-foreground hover:bg-destructive/90">Delete</AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </>
  );
}
